import { Carousel } from "react-responsive-carousel";
import "../App.css";
import Background from "./background";
import Button from "react-bootstrap/Button";
import { Colors } from "../assets/helper";
import { useEffect } from "react";
import BackIcon from "../assets/backicon.png";
import { openInNewTab } from "../assets/helper";
import ImageWithLoader from "./ImageWithLoader";
function DetailsProject({ details, onPressBack }) {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div
      className="styleIn"
      style={{ background: "#f1f1f1", width: "100%", height: "100vh" }}
    >
      <div
        onClick={() => onPressBack()}
        style={{
          display: "flex",
          justifyContent: "center",
          alignContent: "center",
          alignItems: "center",
          position: "absolute",
          top: 10,
          left: 10,
          width: 50,
          height: 50,
          background: Colors.primary,
          borderRadius: 100,
          zIndex:9999999999
        }}
      >
        <i class="fa fa-arrow-left" aria-hidden="true" style={{color:"white"}}/>
      </div>
      {details?.images.length > 0 ? (
        <Carousel autoPlay infiniteLoop>
          {details?.images.map((res, index) => {
            return (
              <div style={{display:"flex",marginLeft:"25%", width: "50%", height: 400 }}>
                <ImageWithLoader
                  alt={`image_${index}`}
                  src={res}
                  style={{
                    width: "100%",
                    height: "100%",
                    objectFit: "contain",
                  }}
                />
              </div>
            );
          })}
        </Carousel>
      ) : null}
      {details?.title ? (
        <Background
          title={<>Project name</>}
          children={
            <>
              <p>{details?.title}</p>
            </>
          }
        />
      ) : null}
      {details?.description ? (
        <Background
          title={<>Description</>}
          children={
            <>
              <p>{details?.description}</p>
            </>
          }
        />
      ) : null}
      <div
        className={
          details?.ProgramingLanguage?.length > 0 && details?.Skills.length > 0
            ? "rowDetails"
            : ""
        }
      >
        {details?.ProgramingLanguage?.length > 0 ? (
          <Background
            title={<>Programing language</>}
            children={
              <>
                {details?.ProgramingLanguage?.map((res) => {
                  return (
                    <div>
                      <b>{res?.type}</b>
                      <p>&#x2022; {res?.language}</p>
                    </div>
                  );
                })}
              </>
            }
          />
        ) : null}
        {details?.Skills.length > 0 ? (
          <Background
            title={<>Skills</>}
            children={
              <>
                {details?.Skills?.map((res) => {
                  return <p>&#x2022; {res}</p>;
                })}
              </>
            }
          />
        ) : null}
      </div>
      {details?.androidlink ? (
        <Button
          onClick={() => {
            openInNewTab(details?.androidlink);
          }}
          style={{
            background: Colors.primary,
            color: "white",
            fontWeight: "bold",
            width: "100%",
            marginBottom: "1%",
          }}
        >
          Download android app
        </Button>
      ) : null}
      {details?.applelink ? (
        <Button
          onClick={() => {
            openInNewTab(details?.applelink);
          }}
          style={{
            background: Colors.primary,
            color: "white",
            fontWeight: "bold",
            width: "100%",
            marginBottom: "1%",
          }}
        >
          Download ios app
        </Button>
      ) : null}
      {details?.weblink ? (
        <Button
          onClick={() => {
            openInNewTab(details?.weblink);
          }}
          style={{
            background: Colors.primary,
            color: "white",
            fontWeight: "bold",
            width: "100%",
            marginBottom: "1%",
          }}
        >
          Visit website
        </Button>
      ) : null}
    </div>
  );
}
export default DetailsProject;
