import "../App.css";

function WorkExperienceComponent({ title, date, isCurrent, description , isLast }) {
  return (
    <div className="w3-container">
      <h5 className="w3-opacity">
        <b>{title}</b>
      </h5>
      {date ? (
        <>
          {isCurrent ? (
            <h6 className="w3-text-teal">
              <i className="fa fa-calendar fa-fw w3-margin-right"></i>
              {date} -{" "}
              <span className="w3-tag w3-teal w3-round">Until now</span>
            </h6>
          ) : (
            <h6 className="w3-text-teal">
              <i className="fa fa-calendar fa-fw w3-margin-right"></i>
              {date}
            </h6>
          )}
        </>
      ) : null}

      {description?.map((res) => {
        return <p style={{textAlign:"center"}}>&#x2022; {res}</p>;
      })}
      {date && !isLast ? <hr /> : null}
    </div>
  );
}
export default WorkExperienceComponent;
