import "../App.css";

function TechnicalSkills({ title, percentage, child, isLast = false }) {
  return (
    <div style={{ width: "100%" }} className="w3-margin-top">
      <p
        style={{
          textAlign: "start",
          fontWeight: child?.length > 0 ? "bold" : "normal",
        }}
      >
        {title}
      </p>
      {child?.map((res, index) => {
        return (
          <div key={index}>
            <>
              <p style={{ textAlign: "start"}}>{res.title}</p>
              {res.percentage && (
                <div className="w3-light-grey w3-round-xlarge w3-small" style={{position:"relative"}}>
                  <div
                    className="w3-container w3-center w3-round-xlarge w3-teal"
                    style={{ width: `${res.percentage}%` }}
                  >
                    <i
                      style={{
                        textAlign: "center",
                        color: "transparent",
                      }}
                    >
                      {res.percentage}%
                    </i>
                  </div>
                  <i style={{ textAlign: "center", position: "absolute" , top:0 , color:"white" }}>
                    {res.percentage}%
                  </i>
                </div>
              )}
              {(res?.isLast || index < child?.length - 1) ? <br /> : null}
            </>
          </div>
        );
      })}
      {!child && (
        <div className="w3-light-grey w3-round-xlarge w3-small">
          <div
            className="w3-container w3-center w3-round-xlarge w3-teal"
            style={{ width: `${percentage}%` }}
          >
            {percentage}%
          </div>
        </div>
      )}
      {isLast ? <br /> : null}
    </div>
  );
}

export default TechnicalSkills;
