import React, { useState } from "react";
import { Img } from "react-image";
import Loader from "./Loader";

const ImageWithLoader = ({ src, alt, style }) => {
  const [isLoading, setIsLoading] = useState(true);

  const handleOnLoad = () => {
    setIsLoading(false);
  };

  return (
    <>
      {isLoading && (
        <div style={{display:"flex" , justifyContent:"center" , alignItems:"center",alignContent:"center" ,alignSelf:"center" , width:"100%" ,height:"100%" , justifyItems:"center" , justifySelf:"center"}}>
          <Loader />
        </div>
      )}
      <Img src={src} alt={alt} onLoad={handleOnLoad} style={style} />
    </>
  );
};

export default ImageWithLoader;
